/*
 *  Promotion
 *    Styles for the promotion section.
 *
 *  Version:
 *    1.0.0 - 2021/10/18
 */

.c-promotion {
  padding: 3.75rem 0 5rem;
  background-color: $s-color--background-subdued;
  overflow-x: hidden;

  &__inner {
    position: relative;
    max-width: 92.75rem;
    margin: 0 auto;
  }

  &__pretitle, &__title {
    padding: 0 $s-gutter--edge-mobile;
    text-align: center;
  }

  // Pre title
  &__pretitle {
    margin-bottom: 0.875rem;
  }

  // Title
  &__title {
    margin-bottom: 2.6875rem;
  }

  // Feature
  &__feature {
    position: relative;
  }

  // Illustration 1
  // Because design dimensions are not setup. 234 X 154 | T: 35 R: -36
  &__illustration-1 {
    &-wrapper {
      position: absolute;
      top: 13.88%;
      right: -9.6%;
      width: 62.4%;
    }

    &-container {
      padding-bottom: 61.81%;
    }
  }

  // Images
  &__images {
    position: relative;
    margin-bottom: 1.875rem;
  }

  // Image Primary
  &__image-primary {
    &-wrapper {
      width: 48.53%;
      margin: 0 auto;
    }

    &-container {
      padding-bottom: 138.46%;
      display: block;

      &.no-pointer{
        cursor: default;
      }
    }
  }

  // Image Secondary 1
  &__image-secondary1 {
    &-wrapper {
      position: absolute;
      top: 8.73%;
      left: 5.33%;
      width: 30.66%;
      z-index: $s-z--text-overlay;
    }

    &-container {
      padding-bottom: 126.08%;
      display: block;

      &.no-pointer{
        cursor: default;
      }
    }
  }

  // Illustration 2
  &__illustration-2 {
    &-wrapper {
      position: absolute;
      top: -15.17%;
      left: -26.95%;
      width: 62.22%;
      z-index: $s-z--text-overlay;
    }

    &-container {
      padding-bottom: 76.05%;
    }
  }

  // Image Secondary 2
  &__image-secondary2 {
    &-wrapper {
      position: absolute;
      top: 42.46%;
      right: 4.8%;
      width: 30.66%;
      z-index: $s-z--text-overlay;
    }

    &-container {
      padding-bottom: 103.47%;
      display: block;

      &.no-pointer{
        cursor: default;
      }
    }
  }

  // CTA
  &__cta-container {
    text-align: center;
  }

  /*** Media Queries ***/
  @include t-media-query($s-medium-up) {
    padding: 6.75rem 0 7.575rem;

    &__pretitle {
      margin-bottom: 1.5rem;
    }

    &__title {
      margin-bottom: 4.875rem;
    }

    &__images {
      margin: 0 auto 0 6%;
    }

    &__illustration-1-wrapper {
      top: 11%;
      right: -3.4%;
      width: 50.63%;
    }

    &__image-primary-wrapper {
      width: 40.23%;
      margin: 0 35.38% 0 26.62%;
    }

    &__image-secondary1-wrapper {
      top: 8.5%;
      left: 2.47%;
      width: 29.26%;
    }

    &__illustration-2-wrapper {
      top: -11.26%;
      left: -13.66%;
      width: 43.66%;
    }

    &__image-secondary2-wrapper {
      top: 31.18%;
      right: 8.83%;
      width: 29.26%;
    }

    &__cta-container {
      position: absolute;
      bottom: 3.92%;
      left: 0;
      width: 100%;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 11.25rem 0 12.625rem;
  }
}
